@charset "UTF-8";
/**
 * This stylesheet is for styles you want to include only when displaying demo
 * styles for grids, animations, color swatches, etc.
 * These styles will not be your production CSS.
 */
/*Add spacing around elements */
#sg-patterns {
  box-sizing: border-box !important;
  max-width: 100%; }

.demo-animate {
  background: #ddd;
  padding: 1em;
  margin-bottom: 1em;
  text-align: center;
  border-radius: 8px;
  cursor: pointer; }

.sg-colors {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  flex-wrap: wrap;
  list-style: none !important;
  padding: 0 !important;
  margin: 0 !important; }

.sg-colors li {
  flex: auto;
  padding: 0.3em;
  margin: 0 0.5em 0.5em 0;
  min-width: 5em;
  max-width: 14em;
  border: 1px solid #ddd;
  border-radius: 8px; }

.sg-swatch {
  display: block;
  height: 4em;
  margin-bottom: 0.3em;
  border-radius: 5px; }

.sg-label {
  font-size: 90%;
  line-height: 1; }

.sg-pattern[id*="atoms"],
.sg-pattern[id*="molecules"],
.sg-pattern[id*="organisms"] {
  padding: 0 2em;
  margin: 1em 0 0;
  position: relative; }

.sg-pattern[id*="templates"] .sg-pattern-head,
.sg-pattern[id*="pages"] .sg-pattern-head,
.sg-pattern[id*="templates"] .sg-pattern-extra,
.sg-pattern[id*="pages"] .sg-pattern-extra {
  margin: 0 2.5rem; }

.sg-pattern[id*="templates"] .sg-pattern-head,
.sg-pattern[id*="pages"] .sg-pattern-head {
  padding: 0.5rem 0 0; }

.sg-subtype {
  padding: 5em 2em 0; }

.sg-pattern-example .nav-primary_overlay-mobile {
  display: none; }

.sg-pattern-example .hub-header {
  position: relative; }

.sg-pattern-example .header-single-task {
  position: relative; }

.sg-pattern-example .jump-nav.fixed {
  position: relative; }

.sg-pattern-example .header-primary--sticky {
  position: relative; }

.layout-example_placeholder {
  background-color: #ededed;
  display: block;
  margin-bottom: 1em;
  padding: 2em;
  text-align: center;
  width: 100%; }

.sg-introduction * {
  max-width: 34em;
  margin-top: 0.4em; }

.sg-introduction code {
  border: solid 1px rgba(0, 0, 0, 0.1);
  background: rgba(0, 0, 0, 0.05);
  border-radius: 5px;
  margin: 1em 0;
  white-space: pre-wrap; }

.sg-introduction-styling blockquote code {
  display: block;
  border: 0;
  padding: 0.5em; }

.pattern-deprecated {
  position: relative;
  background: repeating-linear-gradient(45deg, #e6e6e6, #e6e6e6 10px, rgba(70, 22, 106, 0.27) 10px, rgba(70, 22, 106, 0.27) 20px);
  filter: grayscale(50%); }
  .pattern-deprecated:before {
    content: "PATTERN DEPRECATED — please see Pattern Info";
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 5;
    padding: 0.25em 0.75em;
    background: #3e3e3e;
    color: white;
    font-size: 0.75em;
    border-radius: 1px; }
